import React, {Fragment} from 'react';
import {Row, Col, Card, CardTitle, CardText, Button,
        InputGroup, Input, InputGroupAddon} from 'reactstrap';

class CardsUst extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      messages: [],
      input: ''
    }
  }

  componentDidMount() {
    const {firebase} = this.props;
    const messages = firebase.database().ref('messages/');

    messages.on('value', snapshot => {
      let liste = [];

      Object.keys(snapshot.val()).map(val => {
        let data = {};
        data['id'] = val;
        data['username'] = snapshot.val()[val]['username'];
        data['message'] = snapshot.val()[val]['message'];
        liste.push(data);
      })
      this.setState({
        messages: liste
      })
    })
  }

  updateInput = (e) => {
    this.setState({
      input: e.target.value
    })
  }

  sendMessage = (e) => {
    const {firebase} = this.props;
    const refer = firebase.ref("messages");
    //const sentMes = refer.push({message: this.state.input, username:"berkelmas"});

    console.log("berelmas")
    e.preventDefault();

  }


  render() {
    return (
      <Fragment>
        <Row className="mx-auto d-flex justify-content-center">
          <Col sm="8">
            <Card body>
              <CardTitle className="card-title text-center">Chat Odasi</CardTitle>
              {this.state.messages.map(val =>
                <Card className="p-1 mb-2 mt-2 w-50">
                  <CardText><strong>@{val.username}</strong> {val.message}</CardText>
                </Card>
              )}
              <form onSubmit={this.sendMessage}>
              <InputGroup className="pb-3">
                <Input onChange={this.updateInput} value={this.state.input} placeholder="Canli Chat'e Hosgeldiniz..." />
              </InputGroup>
              <Button type="submit" className="btn btn-info" >GÃ¶nder</Button>
              </form>
            </Card>
          </Col>
        </Row>
      </Fragment>
    )
  }
}

export default CardsUst;
